.left-fadeout-candidates {
    position: absolute;
    /*left: -4px;*/
    top: 0;
    bottom: 0;
    z-index: 2;
    margin-top: 220px;
    margin-bottom: 133px;
    width: 20px;
    background-image: linear-gradient(90deg, #FFF, rgba(255,255,255,0));
    -webkit-background-image: linear-gradient(0deg, #FFF, rgba(255,255,255,0));
}

.right-fadeout-candidates {
    position: absolute;
    right: 0px;
    top: 0;
    margin-top: 220px;
    margin-bottom: 133px;
    bottom: 0;
    z-index: 2;
    width: 20px;
    background-image: linear-gradient(-90deg, #FFF, rgba(255,255,255,0));
    -webkit-background-image: linear-gradient(0deg, #FFF, rgba(255,255,255,0));
}

.bottom-fadeout-candidates {
    bottom: 133px;
    /*margin-right: 11px;*/
    background-image: linear-gradient(0deg, #FFF, rgba(255,255,255,0));
    -webkit-background-image: linear-gradient(0deg, #FFF, rgba(255,255,255,0));
}

.top-fadeout-candidates {
    margin-top: 53px;
    margin-right: 14px;
    background-image: linear-gradient(0deg, rgba(255,255,255,0), #FFF);
    -webkit-background-image: linear-gradient(0deg, rgba(255,255,255,0), #FFF);
}

.bottom-fadeout-candidates {
    height: 25px;
    pointer-events: none;
    position: absolute;
    right: 0;
    left: 0;
    z-index: 2;
}

.top-fadeout-candidates {
    height: 25px;
    pointer-events: none;
    position: absolute;
    right: 0;
    left: 0;
    z-index: 5;
}

